import { request } from '../../request.js'
export default {
	data() {
		return {
			uid: '',
			bank_name: '',
			ask_for_date: '',
			modify_date: '',
			cash: '',
			account_number: '',
			$src: this.$src,
			id: ''
		}
	},
	onLoad(options) {
		this.id = options.id;
		console.log(options);
		this.getInfo()
		// uni.getStorage({
		// 	key: 'uid',
		// 	success: res => {
		// 		console.log(res);
		// 		this.uid = res.data;
		// 		this.getInfo()
		// 	}
		// })
	},
	methods: {
		// 获取提现列表
		getInfo() {
			let that = this;
			request({
				url: 'index.php?s=/wap/member/getMemberWithdrawInfo',
				method: 'post',
				data: {
					id: this.id,
				}
			}).then(res => {
				console.log(res);
				that.bank_name = res.data.msg.bank_name;
				that.ask_for_date = res.data.msg.ask_for_date;
				that.modify_date = res.data.msg.modify_date;
				that.cash = res.data.msg.cash;
				that.account_number = res.data.msg.account_number;
				that.status = res.data.msg.status;
				console.log(res.data.msg)
			})
		},
		
	}
}
